
.head-block {
    background-image: url(../img/mobile/background-mobile.png);
    background-repeat: no-repeat;
    background-position-x: center;
    width:100%;

    &__wrapper{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        max-width: 710px;
        margin: 0 auto;

        color: #fff;
        @extend %oswald-400;

        // border: 3px solid rgb(255, 0, 212);
    }

    &__title{
        font-size: 36px;
        line-height: 36px;
        max-width: 305px;
        padding: 29px 0 20px 0;
    }

    &__text{
        font-size: 14px;
        line-height: 14px;
        text-transform: uppercase;
        padding: 0 0 29px 0;
    }

    &__btn{
        width: 280px;
        height: 40px;

        font-size: 14px;
        line-height: 18px;
        padding-top: 12px;
    }
}

@media (min-width:768px) {

    .head-block{
        background-image: none;
        margin: 0 auto;

        &__wrapper{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: flex-start;
            text-align: start;
            padding: 0 0 0 90px;

            color: #111111;
            @extend %oswald-400;

            position: relative;
    
            // border: 2px solid rgb(255, 0, 230);

        }
    
        &__title{
            font-size: 60px;
            line-height: 60px;
            max-width: 415px;
            padding: 62px 0 33px 0;

        }
    
        &__text{
            font-size: 20px;
            line-height: 20px;
            text-transform: uppercase;
            padding: 0 0 20px 0;
        }

        &__img{
            position: relative;
            top: 0;
            left: -90px;
        }

        &__btn{
            width: 245px;
            height: 52px;
    
            font-size: 20px;
            line-height: 20px;
            padding-top: 17px;

            position: absolute;
            top: 283px;
            left: 90px;
        }
    }
}


@media (min-width:1440px) {
    .body{
        background-image: url(../img/desktop/index-background-desktop.png);
        background-repeat: no-repeat;
        background-position: top right;
        background-size: 720px 696px;
    
    }

    .head-block{

        &__wrapper{
            padding: 0px;
            margin: 0 0 0 83px;
        }

        &__text {
            padding-bottom: 53px;
        }

        &__img {
            position: absolute;
            top: -12px;
            left: 412px;
        }
    
        &__btn{
            left: 0;
            
        }   
    }    
}
