html, body{
    min-width: 320px;
    font-family: 'Arial', sans-serif;
    font-size: 14px;
    color: #444444;

}


.container {   
    min-width: 320px;
    margin: 0 auto;

    padding: 0px 20px 0px 20px;

    // border: 1px solid rgb(0, 0, 0);
}

@media (min-width:768px) {
    .container{
        padding: 0px 31px 0px 30px;

    }
}

@media (min-width:1440px) {
    .container{
        padding: 54px 110px 0px 110px;
    }
}

