.header__row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 11px;
    padding-bottom: 16px;
}

@media (min-width:768px) {
    .header__row{
        display: none;
    }
}

.header-tbl {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 24px;
    padding-bottom: 16px;

    &__link {
        display: flex;
        position: relative;

        text-transform: uppercase;
        @extend %oswald-400;
        font-size: 18px;

        li:not(:last-child){
            margin-right: 42px;
        }
        
        &::before{
            content: "";
            width: 62px;
            height: 2px;
            background-color: $bg-color;

            position: absolute;
            top: 33px;
            left: 0;
        }    
    }
}
@media (max-width:768px) {
    .header-tbl{
        display: none;
    }
}

@media (min-width:1440px) {
    .header-tbl {    
        &__link {
            font-size: 20px;
            color: #fff;
            
            &::before{
                content: "";
                width: 68px;
                height: 2px;
                background-color: #fff;
    
                position: absolute;
                top: 33px;
                left: 1px;
            }    
        }
    }
}



