@charset "UTF-8";
@font-face {
  font-family: "Oswald";
  src: url(../fonts/oswald-regular.woff2), url(../fonts/oswald-regular.woff);
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Arial";
  src: url(../fonts/arial-regular.woff2), url(../fonts/arial-regular.woff);
  font-weight: 400;
  font-style: normal;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, p {
  font-size: inherit;
  font-weight: inherit;
}

input, button {
  font-family: inherit;
  font-size: inherit;
  border: none;
}

button {
  background: none;
  cursor: pointer;
}

input:focus, button:focus {
  outline: none;
}

ul, li {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

html, body {
  min-width: 320px;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  color: #444444;
}

.container {
  min-width: 320px;
  margin: 0 auto;
  padding: 0px 20px 0px 20px;
}

@media (min-width: 768px) {
  .container {
    padding: 0px 31px 0px 30px;
  }
}
@media (min-width: 1440px) {
  .container {
    padding: 54px 110px 0px 110px;
  }
}
.slim__text-catalog, .slim__title, .head-block__wrapper, .header-tbl__link {
  font-family: "Oswald";
  font-weight: 400;
}

.header__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 11px;
  padding-bottom: 16px;
}

@media (min-width: 768px) {
  .header__row {
    display: none;
  }
}
.header-tbl {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 24px;
  padding-bottom: 16px;
}
.header-tbl__link {
  display: flex;
  position: relative;
  text-transform: uppercase;
  font-size: 18px;
}
.header-tbl__link li:not(:last-child) {
  margin-right: 42px;
}
.header-tbl__link::before {
  content: "";
  width: 62px;
  height: 2px;
  background-color: #68b738;
  position: absolute;
  top: 33px;
  left: 0;
}

@media (max-width: 768px) {
  .header-tbl {
    display: none;
  }
}
@media (min-width: 1440px) {
  .header-tbl__link {
    font-size: 20px;
    color: #fff;
  }
  .header-tbl__link::before {
    content: "";
    width: 68px;
    height: 2px;
    background-color: #fff;
    position: absolute;
    top: 33px;
    left: 1px;
  }
}
.head-block {
  background-image: url(../img/mobile/background-mobile.png);
  background-repeat: no-repeat;
  background-position-x: center;
  width: 100%;
}
.head-block__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  max-width: 710px;
  margin: 0 auto;
  color: #fff;
}
.head-block__title {
  font-size: 36px;
  line-height: 36px;
  max-width: 305px;
  padding: 29px 0 20px 0;
}
.head-block__text {
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  padding: 0 0 29px 0;
}
.head-block__btn {
  width: 280px;
  height: 40px;
  font-size: 14px;
  line-height: 18px;
  padding-top: 12px;
}

@media (min-width: 768px) {
  .head-block {
    background-image: none;
    margin: 0 auto;
  }
  .head-block__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    text-align: start;
    padding: 0 0 0 90px;
    color: #111111;
    position: relative;
  }
  .head-block__title {
    font-size: 60px;
    line-height: 60px;
    max-width: 415px;
    padding: 62px 0 33px 0;
  }
  .head-block__text {
    font-size: 20px;
    line-height: 20px;
    text-transform: uppercase;
    padding: 0 0 20px 0;
  }
  .head-block__img {
    position: relative;
    top: 0;
    left: -90px;
  }
  .head-block__btn {
    width: 245px;
    height: 52px;
    font-size: 20px;
    line-height: 20px;
    padding-top: 17px;
    position: absolute;
    top: 283px;
    left: 90px;
  }
}
@media (min-width: 1440px) {
  .body {
    background-image: url(../img/desktop/index-background-desktop.png);
    background-repeat: no-repeat;
    background-position: top right;
    background-size: 720px 696px;
  }
  .head-block__wrapper {
    padding: 0px;
    margin: 0 0 0 83px;
  }
  .head-block__text {
    padding-bottom: 53px;
  }
  .head-block__img {
    position: absolute;
    top: -12px;
    left: 412px;
  }
  .head-block__btn {
    left: 0;
  }
}
.btn {
  display: block;
  color: #fff;
  background-color: #68b738;
  text-transform: uppercase;
  text-align: center;
}

.slim {
  margin-top: 22px;
}

@media (min-width: 768px) {
  .slim {
    margin-top: 0px;
  }
}
.slim__block {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  max-width: 280px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .slim__block {
    max-width: 708px;
  }
}
@media (min-width: 1440px) {
  .slim__block {
    padding-top: 215px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 1220px;
  }
}
.slim__info {
  padding: 39px 20px 22px 20px;
  background-color: #f2f2f2;
  position: relative;
  z-index: -2;
}
.slim__info:first-child {
  margin-bottom: 20px;
}

@media (min-width: 768px) {
  .slim__info {
    padding: 81px 62px 62px 52px;
  }
  .slim__info:first-child {
    margin-bottom: 30px;
  }
  .slim__info::after { /* зеленый кружок 768px */
    content: "";
    display: block;
    width: 200px;
    height: 200px;
    background-color: #68b738;
    border-radius: 50%;
    position: absolute;
    top: 92px;
    right: 62px;
  }
}
@media (min-width: 1440px) {
  .slim__info {
    width: 570px;
  }
  .slim__info:first-child {
    margin-bottom: 0px;
    margin-right: 75px;
  }
  .slim__info::after {
    display: none;
  }
}
.slim__logo-title {
  display: flex;
  align-items: center;
  position: relative;
}
.slim__logo-title::before { /* зеленый кружок */
  content: "";
  display: block;
  width: 50px;
  height: 50px;
  background-color: #68b738;
  border-radius: 50%;
  position: absolute;
  left: 0px;
  z-index: -1;
}

@media (min-width: 768px) {
  .slim__logo-title::before {
    display: none;
  }
}
@media (min-width: 1440px) {
  .slim__logo-title::before {
    display: block;
    width: 100px;
    height: 100px;
  }
}
.slim__logo {
  z-index: 1;
}

@media (min-width: 768px) {
  .slim__logo {
    position: absolute;
    top: 0;
    right: 0;
  }
}
@media (min-width: 1440px) {
  .slim__logo {
    position: absolute;
    top: 0;
    left: 0;
  }
}
.slim__indent-cat {
  width: 35px;
  height: 49px;
  margin: -2px 27px 0 8px;
}

@media (min-width: 768px) {
  .slim__logo {
    width: 140px;
    height: 194px;
  }
}
@media (min-width: 1440px) {
  .slim__logo {
    width: 70px;
    height: 97px;
    margin: -40px 0px 0 12px;
  }
}
.slim__indent-bars {
  width: 67px;
  height: 28px;
  margin: 12px 12px 0 -9px;
}

@media (min-width: 768px) {
  .slim__indent-bars {
    width: 268px;
    height: 112px;
    margin: 70px -40px 0 0px;
  }
}
@media (min-width: 1440px) {
  .slim__indent-bars {
    width: 134px;
    height: 56px;
    margin: -8px 0px 0px -18px;
  }
}
.slim__title {
  color: #111;
  font-size: 24px;
  line-height: 37.02px;
  text-align: left;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .slim__title {
    font-size: 36px;
    line-height: 37.02px;
  }
}
@media (min-width: 1440px) {
  .slim__title {
    margin-left: 164px;
  }
}
.slim__text {
  color: #444;
  font-size: 14px;
  line-height: 18px;
  text-align: left;
  padding-top: 36px;
  padding-bottom: 23px;
  margin-bottom: 20px;
  border-bottom: 1px solid #dadada;
}

@media (min-width: 768px) {
  .slim__text {
    font-size: 16px;
    line-height: 24px;
    max-width: 373px;
    border-bottom: none;
    padding-bottom: 61px;
    margin-bottom: 0;
  }
  .bars-size {
    max-width: 340px;
  }
}
@media (min-width: 1440px) {
  .slim__text {
    max-width: none;
    padding-top: 75px;
    padding-bottom: 36px;
  }
}
.slim__link {
  position: relative;
}

.slim__text-catalog {
  color: #111;
  font-size: 16px;
  line-height: 16px;
  text-align: left;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .slim__text-catalog {
    font-size: 20px;
    line-height: 20px;
  }
}
.slim__arrow {
  position: absolute;
  top: 6px;
  left: 125px;
}