*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

h1, h2, h3, h4, h5, h6, p{
    font-size: inherit;
	font-weight: inherit;

}

input, button{
    font-family: inherit;
    font-size: inherit;
    border: none;
}

button{
    background: none;
    cursor: pointer;
}

input:focus, button:focus{
    outline: none;
}

ul, li {
    list-style: none;
}

a{
    color: inherit;
    text-decoration: none;
}