.slim{
    margin-top: 22px;
    // border: 2px solid rgb(228, 131, 92);
}

@media (min-width:768px) {
    .slim{
        margin-top: 0px;
    } 
}

.slim__block {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    max-width: 280px;
    margin: 0 auto;
}
@media (min-width:768px) {
    .slim__block{
       max-width: 708px;
    }
}
@media(min-width:1440px){
    .slim__block{
        padding-top: 215px;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        max-width: 1220px;
    }
}


.slim__info {
    padding: 39px 20px 22px 20px;
    background-color: $bg-grey;
    position: relative;
    z-index: -2;

    &:first-child{
        margin-bottom: 20px;
    }
}
@media (min-width:768px){
    .slim__info{
        padding: 81px 62px 62px 52px;

        &:first-child{
            margin-bottom: 30px;
        }

        &::after{          /* зеленый кружок 768px */
            content: "";
            display: block;
            width: 200px;
            height: 200px;
            background-color: $bg-color;
            border-radius: 50%;

            position: absolute;
            top: 92px;
            right: 62px;
        }
    }
}
@media(min-width:1440px){
    .slim__info{
        width: 570px;

        &:first-child{
            margin-bottom: 0px;
            margin-right: 75px;
        }

        &::after{  
            display: none;
        }
    }
}


.slim__logo-title{
    display: flex;
    align-items: center;
    position: relative;

    // border: 2px solid rgb(228, 131, 92);


    &::before{             /* зеленый кружок */
        content: "";
        display: block;
        width: 50px;
        height: 50px;
        background-color: $bg-color;
        border-radius: 50%;
        position: absolute;
        left: 0px;
        z-index: -1;
    }
}
@media (min-width:768px){ 
    .slim__logo-title{
    
        &::before{
            display: none;
        }
    }
}
@media (min-width:1440px){ 
    .slim__logo-title{

        &::before{
            display: block;
            width: 100px;
            height: 100px;
        }
        
    }
}


.slim__logo{
    z-index: 1;
}
@media(min-width:768px){
    .slim__logo{
        position: absolute;
        top: 0;
        right: 0;
    }
}
@media(min-width:1440px){
    .slim__logo{
        position: absolute;
        top: 0;
        left: 0;
    }
}


.slim__indent-cat{     
    width: 35px;
    height: 49px;
    margin: -2px 27px 0 8px;
}
@media(min-width:768px){
    .slim__logo{
        width: 140px;
        height: 194px;
    }
}
@media(min-width:1440px){
    .slim__logo{
        width: 70px;
        height: 97px;
        margin: -40px 0px 0 12px;
    }
}


.slim__indent-bars{
    width: 67px;
    height: 28px;
    margin: 12px 12px 0 -9px;
}
@media(min-width:768px){
    .slim__indent-bars{
        width: 268px;
        height: 112px;
        margin: 70px -40px 0 0px;
    }
}
@media(min-width:1440px){
    .slim__indent-bars{
        width: 134px;
        height: 56px;
        margin: -8px 0px 0px -18px;
    }
}


.slim__title {
    color: #111;
    @extend %oswald-400;
    font-size: 24px;
    line-height: 37.02px;
    text-align: left;
    text-transform: uppercase;
}
@media(min-width:768px){
    .slim__title{
        font-size: 36px;
        line-height: 37.02px;
    }
}
@media(min-width:1440px){
    .slim__title{
        margin-left: 164px;
    }
}


.slim__text {
    color: #444;
    font-size: 14px;
    line-height: 18px;
    text-align: left;

    padding-top: 36px;
    padding-bottom: 23px;
    margin-bottom: 20px;
    border-bottom: 1px solid #dadada;
}
@media(min-width:768px){
    .slim__text{
        font-size: 16px;
        line-height: 24px;
        max-width: 373px;
        border-bottom: none;

        padding-bottom: 61px;
        margin-bottom: 0;
        // border: 1px solid rgb(152, 50, 9);
    }
    .bars-size{
        max-width: 340px;
    }
}
@media(min-width:1440px){
    .slim__text{
        max-width: none;
        padding-top: 75px;
        padding-bottom: 36px;

    }
}





.slim__link{
    position: relative;
}

.slim__text-catalog {
    color: #111;
    @extend %oswald-400;
    font-size: 16px;
    line-height: 16px;
    text-align: left;
    text-transform: uppercase;    
}
@media(min-width:768px){
    .slim__text-catalog{
        font-size: 20px;
        line-height: 20px;
    }
}


.slim__arrow{
    position: absolute;
    top: 6px;
    left: 125px;
}

